.login{
  background-image: linear-gradient(to right, #f2709c, #ff9472);
}

.login-page {
    height: 100vh !important;
    width: 100% !important;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .login-form {
    background-color: #b9bcccb6;
    margin-left: -12px;
    padding: 30px;
  }
  
  .login-form h1 {
    text-align: center;
    font-weight: bold;
    margin: 30px;
    color: #344265;
  }
  
  .login-form .ant-form-item-label label {
    color: #344265 !important;
  }
  .login-form button {
    background-color: #344265;
    color: white;
    outline: none;
  }
  .login-form button:hover {
    background-color: black;
    color: white;
  }
  .login-form a {
    color: #344265 !important;
  }
  
  .login-form a:hover {
    color: black !important;
  }